.partners {
    padding: 160px 0 0;
    margin-bottom: 120px;

    &__title {
        margin-bottom: 35px;
    }

    &__wrapper {
        overflow: hidden;
    }

    &__item {
        margin-right: 67px;
    }
    &__wrapper_item {
        display: flex;
        align-items: center;
        height: 50px;
    }
    @media screen and (max-width:768px) {
        padding: 53px 0 0;
        margin-bottom: 64px;
    }
}